
div #wraper {
	height: 100%;
	overflow-y: auto;
}
div #t-box {
	height: 100%;
	padding: 0.5%
}
div #t {
	height: 95%;
	background-color: #e2e4e6;
	margin-top: 40px;
	border-radius: 5px;
}
#item-box {
	width: 90%;
	background-color: #fff;
	margin: 0 auto 10px;
	border-radius: 10px;
	box-shadow:1px 1px 1px rgba(0,0,0,.3)
}
#item-box:hover {
	background-color: #f7f7f7;
}
ul {
	padding: 0;
	margin-top: 10px
}
#item-name {
	border-bottom: 1px solid #dbdbdb;
	padding: 5px 10px
}
#item-intro {
	padding: 10px;
	border-bottom: 1px solid #dbdbdb;
}
#item-time {
	padding: 5px 10px
}
a:hover {
	text-decoration: none;
}
#select {
	margin: 0 5% 0 60%;
	padding-top: 2%
}
.form-control {
	height: 100%;
}
#label {
	margin-left: -100%;
	float: left;
	padding: 6px 0
}



